* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
 
  
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: grey;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 350px;

}

.card p {
  padding: 5px;
  text-align: center;
  font-weight: bold;
}

.card img {
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}